import { observable } from 'mobx'
import { Observer } from 'mobx-react'
import React from 'react'

import { PrimaryButton } from '../../common/Buttons'
import { i18n } from '../../i18n'

interface IProps {
  selection: ID[]
  onConfirm(): Promise<void>
  enabled: boolean
}

export const CartOrderButton: React.SFC<IProps> = ({
  selection,
  onConfirm,
}) => {
  const isConfirming = observable.box(false)

  return (
    <Observer>
      {() => (
        <PrimaryButton
          className="modal__body__button"
          disabled={isConfirming.get()} /*!cartFood.isValid*/
          data-testid="cartOrderButton"
          onClick={async () => {
            isConfirming.set(true)
            await onConfirm()
            isConfirming.set(false)
          }}
        >
          {i18n
            .plural({
              value: selection.length,
              0: 'Order without sidedish',
              one: 'Order with this sidedish',
              other: 'Order with # sidedishes',
            })
            .toLocaleUpperCase()}
        </PrimaryButton>
      )}
    </Observer>
  )
}
