import { Trans } from '@lingui/react'
import React from 'react'

import { GCartView } from '../../graph/types'

interface IProps {
  order: GCartView.Order
}

export const CartDiscounts: React.SFC<IProps> = ({ order }) =>
  order.discounts && order.discounts.length > 0 ? (
    <div>
      {order.discounts.map(
        ({ code, description, price: { formattedValue } }) => (
          <div key={code} className="cart__item__summary">
            <div className="cart__item__summary__label">{description}</div>
            <div className="cart__item__summary__price">{formattedValue}</div>
          </div>
        ),
      )}
      <div className="cart__item__summary">
        <div className="cart__item__summary__label">
          <Trans>Total discount</Trans>
        </div>
        <div className="cart__item__summary__price">
          {order.totalDiscount.formattedValue}
        </div>
      </div>
    </div>
  ) : null
