import './FoodMenuItem.css'

import React from 'react'
import Truncate from 'react-truncate'
import styled from 'styled-components'

import { GFoodMenuRecipe } from '../../graph/types'

import { mediaMobile } from '../../common/Responsive'
import { LinkToCart } from './LinkToCart'

interface IProps {
  recipe: GFoodMenuRecipe.Fragment
  inCart: number
  isActive: boolean
  onClick(): void
  onPrefetch(): void
  canAddToCart: boolean
}

const FoodMenuItemStyled = styled.div`
  padding: 1rem 0.5rem 0.3rem 1.1rem;
  border: 1px solid #d5d5d5 !important;
  margin: 0 0 0.4rem 0;
  border-radius: 0.8rem;
  cursor: pointer;
  position: relative;
  min-height: 6rem;
  &:hover {
    background-color: #c9e8d7;
  }
  background-color: ${({ isActive }: { isActive: boolean }) =>
    isActive ? '#b3ebcc' : 'white'};
  h4 {
    font-size: 1rem;
    font-weight: 700;
    color: #404040;
    margin-bottom: 0.1rem;
    margin-right: 6rem;
  }
  p {
    color: #666666;
    margin: 0.6rem 0 0 0;
    font-size: 0.9rem;
    max-height: 2.5rem;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
`
const FoodMenuItemImage = styled.img`
  width: 6rem;
  height: 6rem;
  display: block;
  float: left;
  margin: -1.05rem 1rem 0 -1.2rem;
  border-radius: 0.8rem 0 0 0.8rem;
  object-fit: cover;
`

const FoodMenuItemPriceCorner = styled.div`
  border-top-width: 1.8rem;
  border-top-style: solid;
  border-top-color: ${({ theme }) => theme.primary} !important;
  position: absolute;
  right: 4.799rem;
  top: 1rem;
  width: 0;
  height: 0;
  border-left: 1.8rem solid transparent;
`

const FoodMenuItemPrice = styled.div`
  background-color: ${({ theme }) => theme.primary} !important;
  position: absolute;
  right: 0;
  top: 1rem;
  width: 4.8rem;
  height: 1.8rem;
  padding: 0.12rem 0.7rem 0.3rem 0rem;
  font-weight: 500;
  font-size: 1.1rem;
  color: white;
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
`

const FoodMenuItemCount = styled.div`
  background-color: ${({ theme }) => theme.secondary};
  float: left;
  position: absolute;
  left: -1.2rem;
  padding: 0.4rem 0.6rem;
  color: white;
  border-radius: 1.8rem;
  font-size: 0.9rem;
  ${mediaMobile`
    left: -0.6rem;
    top: -0.7rem;
  `};
`

const FoodMenuItem: React.SFC<IProps> = ({
  recipe,
  inCart,
  onPrefetch,
  isActive,
  onClick,
  canAddToCart,
}) => (
  <LinkToCart
    recipeId={recipe.id}
    hasSidedish={recipe.sideDishCount > 0}
    canAddToCart={canAddToCart}
    onClick={onClick}
    data-testid={`foodMenuItem__${recipe.id}`}
  >
    <FoodMenuItemStyled onMouseEnter={onPrefetch} isActive={isActive}>
      {recipe.image && (
        <FoodMenuItemImage src={recipe.image} alt={recipe.nameLabel} />
      )}

      <FoodMenuItemPriceCorner />
      <FoodMenuItemPrice data-testid={`foodMenuItem__Price__${recipe.id}`}>
        {recipe.price.formattedValue}
      </FoodMenuItemPrice>
      {inCart > 0 ? <FoodMenuItemCount>{`${inCart}x`}</FoodMenuItemCount> : ''}
      <h4>
        <Truncate lines={2} data-testid={`foodMenuItem__Name__${recipe.id}`}>
          {recipe.nameLabel}
        </Truncate>
      </h4>
      <p>
        <Truncate
          lines={2}
          data-testid={`foodMenuItem__Description__${recipe.id}`}
        >
          {recipe.descriptionLabel}
        </Truncate>
      </p>
    </FoodMenuItemStyled>
  </LinkToCart>
)

export default FoodMenuItem
