import Color from 'color'
import React from 'react'
import styled from 'styled-components'

import { GCartView } from '../../graph/types'
import { i18n } from '../../i18n'

interface IProps {
  order: GCartView.Order
}

const CartItemSummaryTotalThemed = styled.div`
  background: ${({ theme }) =>
    Color(theme.primary)
      .alpha(0.7)
      .hsl()
      .string()} !important;
`

export const CartTotals: React.SFC<IProps> = ({ order }) => (
  <div>
    {order.coverPrice && order.coverPrice.value ? (
      <div key="casePrice" className="cart__item__summary">
        <div className="cart__item__summary__label">{i18n.t`Covers`}</div>
        <div className="cart__item__summary__price">
          {order.coverPrice.formattedValue}
        </div>
      </div>
    ) : null}
    {order.transportFee && order.transportFee.value ? (
      <div key="transportFee" className="cart__item__summary">
        <div className="cart__item__summary__label">{i18n.t`Delivery`}</div>
        <div className="cart__item__summary__price">
          {order.transportFee.formattedValue}
        </div>
      </div>
    ) : null}
    <CartItemSummaryTotalThemed
      key="totalPrice"
      className="cart__item__summary cart__item__summary__total"
    >
      <div className="cart__item__summary__label">{i18n.t`Total`}</div>
      <div
        className="cart__item__summary__price"
        data-testid={`cardItemSummary__Price`}
      >
        {order.totalSum.formattedValue}
      </div>
    </CartItemSummaryTotalThemed>
  </div>
)
