import './CategoryNavigator.css'

import classNames from 'classnames'
import React, { Component } from 'react'
import Scroll from 'react-scroll'
import Swipeable from 'react-swipeable'
import Truncate from 'react-truncate'
import { compose, withState } from 'recompose'

import { Desktop, Mobile } from '../../common/Responsive'
import { GFoodMenuCategory } from '../../graph/types'

interface IProps {
  categories: ReadonlyArray<GFoodMenuCategory.Fragment>
}

interface IInnerProps extends IProps {
  setActiveCategoryIndex(index: number): void
  activeCategoryIndex: number
}

class ContainerByPlatform extends Component {
  state = { scroll: 0 }
  ref: Maybe<HTMLBaseElement> = null
  onMounted = ref => {
    this.ref = ref
  }
  onSwiping = (ev, deltaX) => (this.ref!.scrollLeft += deltaX / 5)
  onSwiped = () => this.setState({ scroll: this.ref!.scrollLeft })
  render() {
    return (
      <>
        <Desktop>
          {isDesktop =>
            isDesktop ? (
              <div className="category__navigator">{this.props.children}</div>
            ) : null
          }
        </Desktop>
        <Mobile>
          <Swipeable
            className="category__navigator category__navigator__nowrap"
            onSwiping={this.onSwiping}
            onSwiped={this.onSwiped}
            innerRef={this.onMounted}
          >
            {this.props.children}
          </Swipeable>
        </Mobile>
      </>
    )
  }
}

export const CategoryNavigator: React.SFC<IProps & IInnerProps> = ({
  categories,
  setActiveCategoryIndex,
  activeCategoryIndex,
}) => (
  <ContainerByPlatform>
    {categories.length === 0
      ? null
      : categories.map((category, idx) => (
          <Scroll.Link
            to={category.nameLabel}
            key={category.id}
            className={classNames('category__navigator__scroll__link', {
              active: activeCategoryIndex === idx,
            })}
            onSetActive={() => setActiveCategoryIndex(idx)}
            offset={-168}
            smooth={true}
            spy={true}
            data-testid={`categoryNavigatorScrollLink__${category.id}`}
          >
            <div className="category__navigator__scroll__link__circle">
              <span className={`flaticon-${category.pictogram}`} />
            </div>
            <div className="category__navigator__scroll__link__description">
              <Truncate lines={2}>{category.nameLabel}</Truncate>
            </div>
          </Scroll.Link>
        ))}
  </ContainerByPlatform>
)

const decorate = compose<IInnerProps, IProps>(
  withState('activeCategoryIndex', 'setActiveCategoryIndex', 0),
)

export default decorate(CategoryNavigator)
