import React from 'react'
import styled, { css } from 'styled-components'

interface IButtonProps {
  color: string
  enabled?: boolean
  data_testid?: string
}

interface IIconProps {
  icon: string
}

interface IProps extends IButtonProps, IIconProps {
  onClick?(): void
}

const CartItemButtonStyled = styled.div<IButtonProps>`
  display: inline-block;
  border-radius: 3rem;
  background: white;
  width: 1.8rem;
  height: 1.8rem;
  text-align: left;
  vertical-align: top;
  padding: 0 0 0 0rem;
  margin: 0.3rem 0.1rem 0 0.1rem;
  ${getButtonColor};
  ${getHoverStyle};
`

function getButtonColor({ color, enabled }: IButtonProps) {
  const useColor = enabled ? color : '#8d8d8d'
  return css`
    color: ${useColor};
    border: 2px solid ${useColor};
  `
}

function getHoverStyle({ enabled }: IButtonProps) {
  return enabled
    ? css`
        cursor: pointer;
        &:hover {
          background: #c9e8d7;
          color: #1d1d1d;
          border-color: #1d1d1d;
        }
      `
    : css`
        cursor: default;
      `
}

const CartItemButtonIcon = styled.span<IIconProps>`
  &:before {
    font-size: 0.8rem;
    margin-left: 0.36rem;
  }
`

export const CartItemButton: React.SFC<IProps> = ({
  color,
  icon,
  onClick,
  enabled = true,
  data_testid,
}) => (
  <CartItemButtonStyled
    color={color}
    enabled={enabled}
    onClick={onClick}
    data-testid={data_testid}
  >
    <CartItemButtonIcon icon={icon} className={`flaticon-${icon}`} />
  </CartItemButtonStyled>
)
