import { Trans } from '@lingui/react'
import { Observer } from 'mobx-react'
import React from 'react'
import styled from 'styled-components'

import { mediaDesktop, mediaMobile } from '../../common/Responsive'
import { GFoodMenu } from '../../graph/types'
import { TRootModel } from '../../models/RootModel'
import FoodCategory from './FoodCategory'
import FoodMenuItem from './FoodMenuItem'

interface IProps {
  categories: ReadonlyArray<GFoodMenu.WareCategories>
  inCart: { [recipeId: string]: number }
  orderItemCount: number
  shouldUpdate: boolean
  rootModel: TRootModel
}

const FoodMenuStyled = styled.div`
  border-radius: 0 0 0.6rem 0.6rem;
  ${mediaDesktop`background: white;`};
  ${mediaMobile`
    background: #eee9e7;
    background-size: cover;
    background-attachment: scroll;
  `};
`

const FoodMenuListStyled = styled.div`
  ${mediaDesktop`
    margin: 0 1.4rem 1rem 1.6rem;
    padding: 2rem 0 2rem 0;
  `};
  ${mediaMobile`
    margin: 0 0.7rem 1rem 0.7rem;
    padding: 0 0 4rem 0;
  `};
`

export class FoodMenu extends React.Component<IProps> {
  shouldComponentUpdate({ shouldUpdate }: IProps) {
    return shouldUpdate
  }
  orderLimitReached() {
    const {
      orderItemCount,
      rootModel: { config },
    } = this.props
    return orderItemCount >= config.orderItemsMax
  }
  render() {
    const {
      categories,
      inCart,
      rootModel: { order, user },
    } = this.props
    const canAddToCart = !this.orderLimitReached()
    return (
      <FoodMenuStyled>
        <FoodMenuListStyled>
          {categories.length === 0 ? (
            <h3>
              <Trans>Food menu is empty. We are sorry.</Trans>
            </h3>
          ) : (
            <Observer>
              {() =>
                categories.map(category => (
                  <FoodCategory key={category.id} category={category}>
                    {category.recipes.map(recipe => (
                      <FoodMenuItem
                        key={recipe.id}
                        recipe={recipe}
                        inCart={inCart[recipe.id] || 0}
                        isActive={order.lastActiveRecipeId === recipe.id}
                        canAddToCart={canAddToCart}
                        onClick={() => {
                          if (canAddToCart) {
                            order.setUsedRecipeId(recipe.id)
                          } else {
                            user.showOrderLimit()
                          }
                        }}
                        onPrefetch={() => {
                          if (recipe.sideDishCount > 0) {
                            order.prefetchFood(recipe.id)
                          }
                        }}
                      />
                    ))}
                  </FoodCategory>
                ))
              }
            </Observer>
          )}
        </FoodMenuListStyled>
      </FoodMenuStyled>
    )
  }
}
