import { Trans } from '@lingui/react'
import { Observer } from 'mobx-react'
import React from 'react'
import { Alert, Button } from 'reactstrap'
import styled from 'styled-components'

import { mediaDesktop, mediaMobile } from '../../common/Responsive'
import { WithRoot } from '../../common/WithRoot'
import { QueryOrderForm } from './OrderFormView.graphql'

interface IProps {}

const NoticeButton = styled(Button)`
  font-size: 0.9rem;
  margin: 0.2rem 1rem;
  padding: 0.5em 1rem;
`

const StorageNotice = styled(Alert)`
  width: 100%;
  z-index: 5;
  ${mediaDesktop`
    font-size: 0.9rem;
    position: sticky;
    bottom: 0;
  `};
  ${mediaMobile`
    position: fixed;
    top: 5rem;
    font-size: 0.8rem;
    `};
  padding: 0.5rem 1rem;
  background-color: #d4eddadc;
`

export const OrderFormStorageNotice: React.SFC<IProps> = props => (
  <WithRoot
    render={root => (
      <QueryOrderForm
        skip={!root.order.hasOrder || root.user.rememberForm !== null}
        variables={{ orderId: root.order.orderId! }}
        renderLoading={() => null}
        render={({ order }) => (
          <Observer>
            {() =>
              order.countWares > 0 && root.user.rememberForm === null ? (
                <StorageNotice data-testid="orderFormStorageNotice">
                  <p>
                    <Trans>
                      This app is using secure storage for keeping filled form
                      data. It's not cookies, these are stored in your browser
                      only.
                    </Trans>
                  </p>

                  <NoticeButton
                    color="success"
                    size="sm"
                    onClick={root.user.setFormRememberYes}
                    data-testid="orderFormStorageNotice__Yes"
                  >
                    <Trans>Always remember</Trans>
                  </NoticeButton>
                  <NoticeButton
                    color="secondary"
                    size="sm"
                    onClick={root.user.setFormRememberNo}
                    data-testid="orderFormStorageNotice__No"
                  >
                    <Trans>Do not store</Trans>
                  </NoticeButton>
                </StorageNotice>
              ) : null
            }
          </Observer>
        )}
      />
    )}
  />
)
