import React from 'react'
import { Link } from 'react-router-dom'

import { CartItemButton } from './CartItemButton'

interface IProps {
  itemId: ID
  enabled: boolean
  onClick(): void
}

export const EditCartItemButton: React.SFC<IProps> = ({
  itemId,
  onClick,
  ...props
}) => {
  const button = <CartItemButton icon="edit" color="#d9a700" {...props} />
  return props.enabled ? (
    <Link
      to={`/order/change/${itemId}`}
      onClick={onClick}
      data-testid={`editCartItemButton__${itemId}`}
    >
      {button}
    </Link>
  ) : (
    button
  )
}
