import React from 'react'

import { WithOrder } from '../../common/WithOrder'
import { GCartPresence } from '../../graph/types'
import CategoryNavigator from './CategoryNavigator'
import { FoodMenu } from './FoodMenu'
import {
  QueryFoodMenu,
  QueryFoodMenuCartPresence,
} from './FoodMenuView.graphql'
import { OrderLimitModal } from './OrderLimitModal'

export const FoodMenuView: React.SFC = () => (
  <WithOrder
    render={orderModel => (
      <QueryFoodMenu
        variables={{
          branchId: orderModel.branchId!,
        }}
        render={({ wareCategories }) => (
          <>
            <OrderLimitModal />
            <CategoryNavigator categories={wareCategories} />
            <QueryFoodMenuCartPresence
              variables={{ orderId: orderModel.orderId! }}
              renderLoading={false}
              render={({ order }, { loading }) => (
                <FoodMenu
                  categories={wareCategories}
                  rootModel={orderModel.root}
                  inCart={getCartPresence(order && order.items)}
                  orderItemCount={order ? order.items.length : 0}
                  shouldUpdate={Boolean(order && !loading)}
                />
              )}
            />
          </>
        )}
      />
    )}
  />
)

function getCartPresence(items: ReadonlyArray<GCartPresence.Items>) {
  if (!items) {
    return {}
  }
  return items.reduce((result, item) => {
    const recipeId = item.recipe.id
    if (!result[recipeId]) {
      result[recipeId] = 0
    }
    result[recipeId] += 1
    return result
  }, {})
}
