import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { GoogleMap, GoogleMapProps, withGoogleMap } from 'react-google-maps'
import styled from 'styled-components'

import { TMapModel } from '../models/MapModel'

interface IBaseMapProps extends GoogleMapProps {
  model: TMapModel
}

const MapContainerStyled = styled.div`
  border-radius: 0 0 0.5rem 0.5rem;
  overflow: hidden;
  height: calc(100% - 3.75rem);
`

const MapElement = styled.div`
  height: 100%;
  position: relative;
`

const BaseMapContainer = withGoogleMap(({ children }) => <>{children}</>)

const getMapOptions = (): google.maps.MapOptions => {
  return {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
    zoomControlOptions: {
      position: google.maps.ControlPosition.RIGHT_TOP,
    },
  }
}

export const BaseMap: React.FC<IBaseMapProps> = ({ model, children }) => {
  return useObserver(() => (
    <BaseMapContainer
      containerElement={
        <MapContainerStyled
          className="map__container"
          data-testid="googleMap"
        />
      }
      mapElement={<MapElement />}
    >
      <GoogleMap
        ref={model.setMapRef}
        zoom={model.zoom}
        center={model.center}
        onCenterChanged={model.updatePosition}
        onZoomChanged={model.updateZoom}
        onClick={model.onClick}
        options={getMapOptions()}
      >
        {children}
      </GoogleMap>
    </BaseMapContainer>
  ))
}
