import { differenceInMinutes } from 'date-fns'
import { PureComponent } from 'react'

import { GCompanyBranchStatus } from '../graph/types'

export type TStatus = GCompanyBranchStatus.Fragment

export type TStatusDecision = 'Open' | 'Closed' | 'ClosingSoon' | 'OpeningSoon'

export interface IShopStatusRender {
  decision: TStatusDecision
  openingIn: number
  closingIn: number
}

interface IProps {
  status: TStatus
  render(opts: IShopStatusRender): ReactNode
  openThresholdInMinutes?: number
  closeThresholdInMinutes?: number
  timedUpdate?: boolean
  now?: Date
}

export class ShopStatus extends PureComponent<IProps> {
  static defaultProps = {
    openThresholdInMinutes: 30,
    closeThresholdInMinutes: 30,
  }
  timer: Maybe<NodeJS.Timer> = null
  componentDidMount() {
    if (!this.props.timedUpdate) {
      return
    }
    const update = () => {
      this.forceUpdate()
      this.timer = setTimeout(update, 30 * 1000)
    }
    update()
  }
  componentWillUnmount() {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }
  render() {
    const {
      status,
      render,
      now = new Date(),
      openThresholdInMinutes,
      closeThresholdInMinutes,
    } = this.props

    const { isOpen, willCloseAt, willOpenAt } = status

    let decision: TStatusDecision

    if (isOpen) {
      decision = isSoon(willCloseAt, closeThresholdInMinutes!, now)
        ? 'ClosingSoon'
        : 'Open'
    } else {
      decision = isSoon(willOpenAt, openThresholdInMinutes!, now)
        ? 'OpeningSoon'
        : 'Closed'
    }

    return render({
      decision,
      get openingIn() {
        return willOpenAt ? differenceInMinutes(willOpenAt, new Date()) : 0
      },
      get closingIn() {
        return willCloseAt ? differenceInMinutes(willCloseAt, new Date()) : 0
      },
    })
  }
}

function isSoon(time: Maybe<string>, threshold: number, now: Date) {
  return time ? Math.abs(differenceInMinutes(time, now)) <= threshold : false
}
