import './CartItem.css'

import React from 'react'
import styled from 'styled-components'

import { Desktop } from '../../common/Responsive'
import { GOrder } from '../../graph/types'
import { i18n } from '../../i18n'
import { EditCartItemButton } from './EditCartItemButton'
import { RemoveCartItemButton } from './RemoveCartItemButton'

interface IProps {
  item: GOrder.Items
  isActive: boolean
  onEdit(): void
}

const EditButtonsContainerStyled = styled.div`
  flex: 0 0 auto;
`

const CartItemStyled = styled.div`
  width: 100%;
  min-height: 3rem;
  color: #1d1d1d;
  margin: 0 0 0.4rem 0;
  padding: 0.2rem;
  background: ${({ isActive }: { isActive: boolean }) =>
    isActive ? '#b3ebcc' : 'rgba(255, 255, 255, 0.8)'};
  text-align: left;
  display: flex;
`

export const CartItem: React.SFC<IProps> = ({ item, isActive, onEdit }) => (
  <CartItemStyled isActive={isActive}>
    <EditButtonsContainerStyled>
      <RemoveCartItemButton itemId={item.id} enabled={!item.automatic} />

      <Desktop>
        <EditCartItemButton
          itemId={item.id}
          enabled={item.recipe.sideDishCount !== 0 && !item.automatic}
          onClick={onEdit}
        />
      </Desktop>
    </EditButtonsContainerStyled>

    <div className="cart__item__description">
      <div
        className="cart__item__item__name"
        data-testid={`cardItem__Name__${item.recipe.id}`}
      >
        {item.recipe.nameLabel}
      </div>
      <div
        className="cart__item__item__side__dish"
        data-testid={`cardItem__SideDish__${item.recipe.id}`}
      >
        {item.sideDishes.length > 0
          ? `+ ${item.sideDishes
              .map(({ recipe }) => recipe.nameLabel)
              .join(',')}`
          : item.recipe.sideDishCount > 0 && (
              <span>{i18n.t`Without sidedish`.toLocaleLowerCase()}</span>
            )}
      </div>
    </div>

    <div
      className="cart__item__item__price"
      data-testid={`cardItem__Price__${item.id}`}
    >
      {item.automatic ? i18n.t`For free` : item.price.formattedValue}
    </div>
  </CartItemStyled>
)
