import './CartView.css'

import React from 'react'

import { WithOrder } from '../../common/WithOrder'
import { AddToCartModal } from './AddToCartModal'
import { CartContents } from './CartContents'
import { CartIsEmpty } from './CartIsEmpty'
import { QueryCartView } from './CartView.graphql'
import { ChangeCartModal } from './ChangeCartModal'

interface IProps {
  onContentChange?(): void
}

export const CartView: React.SFC<IProps> = () => (
  <WithOrder
    redirect={true}
    render={orderModel => (
      <>
        <AddToCartModal order={orderModel} />
        <ChangeCartModal root={orderModel.root} />
        <QueryCartView
          variables={{ orderId: orderModel.orderId! }}
          renderLoading={false}
          render={({ order }) => (
            <>
              {!order || order.countWares === 0 ? (
                <CartIsEmpty />
              ) : (
                <CartContents order={order} rootModel={orderModel.root} />
              )}
            </>
          )}
        />
      </>
    )}
  />
)
