import React from 'react'

import { WithRoot } from '../../common/WithRoot'
import { CartItemButton } from './CartItemButton'

interface IProps {
  itemId: ID
  enabled: boolean
}

export const RemoveCartItemButton: React.SFC<IProps> = ({
  itemId,
  enabled,
}) => (
  <WithRoot
    render={root => (
      <CartItemButton
        icon="delete"
        color="#99230e"
        enabled={enabled}
        data_testid={`removeCartItemButton`}
        onClick={() => root.order.removeItemFromCart(itemId)}
      />
    )}
  />
)
