import './OrderSubmitLine.css'

import React from 'react'

import { PrimaryButton } from '../../common/Buttons'
import { Desktop, Mobile } from '../../common/Responsive'
import SubmitSpinner from '../../common/SubmitSpinner'
import { WithOrder } from '../../common/WithOrder'
import { i18n } from '../../i18n'

interface IProps {
  onClick(): void
  canSubmit: boolean
}

export const OrderSubmitLine: React.SFC<IProps> = ({
  onClick,
  canSubmit = false,
}) => (
  <WithOrder
    observe={true}
    render={order => (
      <div className="order__form__submit__line">
        <div className="order__form__submit__line__strip" />
        <PrimaryButton
          className="order__form__submit__line__button"
          color="primary"
          size="md"
          onClick={onClick}
          disabled={!canSubmit || order.isSubmitting}
          data-testid="orderSubmitButton"
        >
          <Desktop>
            {order.isSubmitting ? (
              <SubmitSpinner />
            ) : (
              i18n.t`Place order`.toLocaleUpperCase()
            )}
          </Desktop>
          <Mobile>{i18n.t`Place order`.toLocaleUpperCase()}</Mobile>
        </PrimaryButton>
      </div>
    )}
  />
)
