import React from 'react'
import { Redirect } from 'react-router'

import { TOrderModel } from '../models/OrderModel'
import { WithRoot } from './WithRoot'

interface IProps {
  render(root: TOrderModel): ReactNode
  observe?: boolean
  redirect?: boolean
}

export const WithOrder: React.SFC<IProps> = ({
  render,
  observe,
  redirect = false,
}) => (
  <WithRoot
    observe={observe}
    render={({ order }) => {
      return order.hasOrder ? (
        render(order)
      ) : redirect ? (
        <Redirect to="/" />
      ) : null
    }}
  />
)
