import React from 'react'

import { WithOrder } from '../../common/WithOrder'
import { GFinishView } from '../../graph/types'

import { QueryFinishView } from './FinishView.graphql'

interface IProps {
  render(result: GFinishView.Query): ReactNode
}

export const FinishViewLoader: React.SFC<IProps> = ({ render }) => (
  <WithOrder
    redirect={true}
    render={order => (
      <QueryFinishView
        variables={{
          branchId: order.branchId!,
          orderId: order.orderId!,
        }}
        render={data => render(data)}
      />
    )}
  />
)
