import { Trans } from '@lingui/react'
import { Observer } from 'mobx-react'
import React from 'react'
import styled from 'styled-components'

import { AlertModal } from '../../common/AlertModal'
import Pacman from '../../common/Pacman'
import { TOrderModel } from '../../models/OrderModel'

interface IProps {
  order: TOrderModel
}

const TextPadding = styled.div`
  padding-bottom: 2rem;
`

export const OrderGoPayModal: React.SFC<IProps> = ({ order }) => (
  <Observer>
    {() => (
      <AlertModal isOpen={order.isGoPayChecking} headerText="GoPay platba">
        <TextPadding>
          <Trans>Checking your GoPay payment, please wait...</Trans>
        </TextPadding>
        <Pacman noMargin={true} />
      </AlertModal>
    )}
  </Observer>
)
