import { Observer } from 'mobx-react'
import React from 'react'

import { GCartView } from '../../graph/types'

import { TRootModel } from '../../models/RootModel'
import { CartDiscounts } from './CartDiscounts'
import { CartItem } from './CartItem'
import { CartTotals } from './CartTotals'

interface IProps {
  order: GCartView.Order
  rootModel: TRootModel
}

export const CartContents: React.SFC<IProps> = ({ order, rootModel }) => (
  <div className="cart__items__container">
    <div className="cart__items__container__content">
      <Observer>
        {() =>
          order.items.map(item => (
            <CartItem
              key={item.id}
              item={item}
              isActive={rootModel.order.lastActiveRecipeId === item.recipe.id}
              onEdit={() => rootModel.order.setUsedRecipeId(item.recipe.id)}
            />
          ))
        }
      </Observer>
      <CartDiscounts order={order} />
      <CartTotals order={order} />
    </div>
  </div>
)
