import gql from 'graphql-tag'

import { buildQuery } from '../../graph'
import { CartItemFragment, PriceFragment } from '../../graph/fragments.graphql'
import { GFinishView } from '../../graph/types'

export const FinishViewOrderFragment = gql`
  fragment GFinishViewOrder on Order {
    id
    items {
      ...GCartItem
      sideDishes {
        ...GCartItem
      }
    }
    totalSum {
      ...GPrice
    }
    orderPayments {
      paymentMethod {
        paymentType {
          enum
        }
      }
    }
  }
  ${CartItemFragment}
  ${PriceFragment}
`

export const FinishViewQuery = gql`
  query GFinishView($orderId: ID!, $branchId: ID!) {
    order(id: $orderId) {
      ...GFinishViewOrder
    }
    companyBranch(id: $branchId) {
      id
      phone
      company {
        id
      }
    }
    androidApp: mobileApplicationSettings(
      companyBranch: $branchId
      platform: ANDROID
    ) {
      url
    }
    iosApp: mobileApplicationSettings(companyBranch: $branchId, platform: IOS) {
      url
    }
  }
  ${FinishViewOrderFragment}
`

export const QueryFinishView = buildQuery<
  GFinishView.Query,
  GFinishView.Variables
>(FinishViewQuery)
