import './SidedishPicker.css'

import { Observer } from 'mobx-react'
import React, { Fragment } from 'react'

import { GCartRecipe } from '../../graph/types'

import CategoryValidationMessage from './CategoryValidationMessage'
import { SidedishCheckbox } from './SidedishCheckbox'

interface IProps {
  recipe: GCartRecipe.Fragment
  selection: ReadonlyArray<ID>
  onToggle(item: ID): void
}

export const SidedishPicker: React.SFC<IProps> = ({
  recipe,
  selection,
  onToggle,
}) => (
  <Observer>
    {() => (
      <div className="food__sidedish__picker">
        {recipe.categories.map(category => (
          <Fragment key={category.id}>
            <div className="food__sidedish__picker__category">
              {category.nameLabel}{' '}
              <CategoryValidationMessage
                minCount={category.minCount}
                maxCount={category.maxCount}
              />
            </div>
            {category.sideDishes.map(sidedish => (
              <SidedishCheckbox
                key={`${sidedish.id}-${category.id}`}
                sidedish={sidedish}
                isSelected={selection.includes(sidedish.id)}
                canBeSelected={true}
                onClick={() => onToggle(sidedish.id)}
              />
            ))}
          </Fragment>
        ))}
      </div>
    )}
  </Observer>
)
