import { Plural, Trans } from '@lingui/react'
import React from 'react'
import styled from 'styled-components'

import { ShopStatus } from '../../common/ShopStatus'
import { GCompanyBranchStatus } from '../../graph/types'

interface IProps {
  status?: GCompanyBranchStatus.Fragment
}

const StatusDisplayOpen = styled.span`
  font-style: italic;
  color: #0f9849;
`

const StatusDisplayClosed = styled.span`
  font-style: italic;
  color: #b40000;
`

export const ShopStatusDisplay: React.SFC<IProps> = ({ status }) =>
  status ? (
    <ShopStatus
      status={status}
      render={s => {
        switch (s.decision) {
          case 'Open':
            return (
              <StatusDisplayOpen data-testid="statusDisplayOpen">
                <Trans>We are open</Trans>
              </StatusDisplayOpen>
            )
          case 'OpeningSoon':
            return (
              <StatusDisplayClosed>
                <Plural
                  value={s.openingIn}
                  one="Opening in 1 minute"
                  other="Opening in # minutes"
                />
              </StatusDisplayClosed>
            )
          case 'ClosingSoon':
            return (
              <StatusDisplayOpen>
                <Plural
                  value={s.closingIn}
                  one="Closing in 1 minute"
                  other="Closing in # minutes"
                />
              </StatusDisplayOpen>
            )
          case 'Closed':
            return (
              <StatusDisplayClosed>
                <Trans>Closed</Trans>
              </StatusDisplayClosed>
            )
        }
      }}
    />
  ) : null
