import './SidedishCheckbox.css'

import React from 'react'
import { Button } from 'reactstrap'
import styled from 'styled-components'

import { GCartRecipe } from '../../graph/types'

interface IProps {
  sidedish: GCartRecipe.SideDishes
  isSelected: boolean
  canBeSelected: boolean
  onClick(): void
}

const SidedishCheckboxThemed = styled(
  ({ isSelected, canBeSelected, ...props }) => <Button {...props} />,
)`
  background-color: ${props =>
    props.isSelected ? props.theme.tertiary : 'transparent'} !important;
  color: ${props =>
    props.isSelected ? props.theme.tertiaryText : 'initial'} !important;
  &:hover {
    border-color: ${({ theme }) => theme.tertiaryHover} !important;
  }
`

export const SidedishCheckbox: React.SFC<IProps> = props => (
  <SidedishCheckboxThemed
    size="xs"
    outline={true}
    data-testid={`sidedishCheckbox__${props.sidedish.id}`}
    disabled={!props.canBeSelected && !props.isSelected}
    {...props}
  >
    <div className="food__sidedish__picker__button__name">
      {props.sidedish.nameLabel}
    </div>
    <div className="food__sidedish__picker__button__price">
      {props.sidedish.price.formattedValue}
    </div>
  </SidedishCheckboxThemed>
)
