import { ApolloError } from 'apollo-client'
import { DocumentNode, GraphQLError } from 'graphql'
import { useCallback } from 'react'
import { MutationHookOptions, useMutation } from 'react-apollo-hooks'

import { RM } from '../helpers/ramda'
import { reportError } from '../helpers/sentry'
import { i18n } from '../i18n'

interface IGuardedMutationOptions<TData, TVariables>
  extends MutationHookOptions<TData, TVariables> {}

export function useGuardedMutation<TData, TVariables>(
  mutation: DocumentNode,
  options: IGuardedMutationOptions<TData, TVariables> = {},
) {
  const defaultMessage = i18n.t`Unexpected error occured while sending a request. Our team was notified about the issue.`

  const handleGraphQLErrors = useCallback(
    (errors: ReadonlyArray<GraphQLError>) => {
      const userErrors = errors.filter(RM.propEq('showToUser', true))
      if (userErrors.length === 0) {
        window.alert(defaultMessage)
      } else {
        window.alert(userErrors.map(RM.prop('message')).join('\n'))
      }
    },
    [],
  )

  const handleError = useCallback((err: Error) => {
    if (isApolloError(err)) {
      if (err.networkError) {
        window.alert(
          i18n.t`Communication with server has failed. Please retry the operation in a moment again.`,
        )
        return
      } else if (err.graphQLErrors) {
        handleGraphQLErrors(err.graphQLErrors)
        return
      }
    }
    reportError(err)
  }, [])

  const mutate = useMutation(mutation, options)
  return async (variables: TVariables) => {
    try {
      const result = await mutate({ variables })
      if (result.errors) {
        handleGraphQLErrors(result.errors)
        return null
      }
      if (!result.data) {
        return null
      }
      return result.data as TData
    } catch (err) {
      handleError(err)
      return null
    }
  }
}

function isApolloError(err: Error): err is ApolloError {
  return err.hasOwnProperty('graphQLErrors')
}
