import { useDebounceCallback } from '@speedlo/hooks'
import React, { KeyboardEvent, useCallback, useRef, useState } from 'react'
import { useQuery } from 'react-apollo-hooks'
import { FormGroup, Input, InputGroup } from 'reactstrap'
import styled from 'styled-components'

import { SecondaryButton } from '../../common/Buttons'
import { useRoot } from '../../common/WithRoot'
import {
  GOrderPromoCodes,
  GPromoCode,
  MAddOrderPromo,
  MRemoveOrderPromo,
} from '../../graph/types'
import { useGuardedMutation } from '../../hooks/useGuardedMutation'
import { i18n } from '../../i18n'
import {
  AddOrderPromoMutation,
  OrderPromoCodesQuery,
  RemoveOrderPromoMutation,
} from './OrderPromoInput.graphql'

type TPromoCode = GPromoCode.Fragment

interface IProps {
  codes: ReadonlyArray<TPromoCode>
  onCodeAdd(codeInput: string): Promise<void>
  onCodeRemove(codeId: ID): void
  autoSubmitAfterTimeout?: number
}

const CodesPanel = styled.div`
  margin-top: 0.5rem;
  & button {
    margin-right: 0.5rem;
    font-size: 0.7rem;
  }
`

export function OrderPromoInputUI({
  codes,
  onCodeAdd,
  onCodeRemove,
  autoSubmitAfterTimeout = 2000,
}: IProps) {
  const inputRef = useRef<HTMLInputElement>(null)
  const [isValidating, setValidating] = useState(false)

  const addCode = useCallback(
    () => {
      if (inputRef.current && !inputRef.current.disabled) {
        const code = inputRef.current.value.trim()
        if (code.length > 0) {
          setValidating(true)
          inputRef.current.disabled = true
          inputRef.current.value = i18n.t`Validating promo code ${code}...`
          onCodeAdd(code)
            .then(() => {
              setValidating(false)
              if (inputRef.current) {
                inputRef.current.value = ''
              }
            })
            .catch(() => {
              if (inputRef.current) {
                inputRef.current.value = code
              }
            })
            .finally(() => {
              setValidating(false)
              if (inputRef.current) {
                inputRef.current.disabled = false
                inputRef.current.focus()
              }
            })
        }
      }
    },
    [onCodeAdd],
  )

  const onChange = useDebounceCallback(addCode, autoSubmitAfterTimeout)

  const onKeyDown = useCallback((ev: KeyboardEvent<HTMLInputElement>) => {
    if (ev.key === 'Enter') {
      addCode()
      ev.preventDefault()
    }
  }, [])

  return (
    <FormGroup>
      <InputGroup>
        <Input
          innerRef={inputRef}
          autoComplete="off"
          disabled={isValidating}
          placeholder={i18n.t`Promo codes`}
          onChange={onChange}
          onKeyDown={onKeyDown}
          onBlur={addCode}
          onPaste={addCode}
        />
      </InputGroup>
      <CodesPanel>
        {codes.map(({ id, code }) => (
          <SecondaryButton key={id} onClick={() => onCodeRemove(id)}>
            {`${code} `}
          </SecondaryButton>
        ))}
      </CodesPanel>
    </FormGroup>
  )
}

const OrderPromoInput: React.FC = () => {
  const { order } = useRoot()

  const { data } = useQuery<GOrderPromoCodes.Query, GOrderPromoCodes.Variables>(
    OrderPromoCodesQuery,
    {
      suspend: false,
      variables: { orderId: order.orderId! },
    },
  )

  const addCodeMutation = useGuardedMutation<
    MAddOrderPromo.Mutation,
    MAddOrderPromo.Variables
  >(AddOrderPromoMutation, {
    refetchQueries: ['GCartView'],
  })

  const onCodeAdd = useCallback(async (code: string) => {
    if (!order.orderId) {
      return
    }
    const result = await addCodeMutation({
      orderId: order.orderId!,
      code: code,
    })
    if (result && result.addOrderPromoCode.userErrors.length) {
      order.submitFailed(result.addOrderPromoCode.userErrors)
    }
  }, [])

  const removeCodeMutation = useGuardedMutation<
    MRemoveOrderPromo.Mutation,
    MRemoveOrderPromo.Variables
  >(RemoveOrderPromoMutation, { refetchQueries: ['GCartView'] })

  const onCodeRemove = (codeId: ID) => {
    if (order.orderId) {
      removeCodeMutation({ codeId, orderId: order.orderId })
    }
  }

  return (
    <OrderPromoInputUI
      codes={data && data.order ? data.order.promoCodes : []}
      onCodeAdd={onCodeAdd}
      onCodeRemove={onCodeRemove}
    />
  )
}

export default OrderPromoInput
